import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Router } from '../../../node_modules/@angular/router';

@Component({
  selector: 'app-dir-map',
  templateUrl: './dir-map.component.html',
  styleUrls: ['./dir-map.component.css']
})

export class DirMapComponent implements OnInit {

  hospData: any = [];
  lat: number;
  lng: number;
  victimLabel = 'X';
  HospitalLabel = 'H';
  origin: any = {};
  destination: any = {};
  currentHospData: HospitalData;
  query: string;
  status: any;
  constructor(private obj: Router, private http: HttpClient) { }

  ngOnInit() {

    navigator.geolocation.getCurrentPosition(
      (position) => {
        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;
      },
      (error) => {
        switch (error.code) {
            case error.PERMISSION_DENIED:
                console.log('Error: User denied the request for Geolocation.');
                break;
            case error.POSITION_UNAVAILABLE:
                 console.log('Error: Location information is unavailable.');
                 break;
            case error.TIMEOUT:
                 console.log('Error: The request to get user location timed out.');
                 break;
          }
          this.lat = 0;
          this.lng = 0;
          this.hospData = [];
          alert('Location Information is required');
          this.obj.navigate(['/latlng']);

      });
  }

  getdata(query) {
    this.query = query;
    this.http.get('/latlng/' + query + ',' + this.lat + ',' + this.lng).subscribe((response) => {
      this.hospData = response;
    });
  }

  myMap(data) {
    this.currentHospData = data;
    this.origin = { lat: this.currentHospData.lat, lng: this.currentHospData.lng };
    this.destination = { lat: this.lat, lng: this.lng };
    const url = this.currentHospData.placeId + ',' + this.lat + ',' + this.lng;
    this.http.get('/latlng/hosp/' + url).subscribe((response: any) => {
      this.status = JSON.parse(response);
      console.log(this.status);
      console.log(this.status.status);

      if (this.status.status === 'failure') {
        window.alert('Alert sent to corresponding ' + this.query);
        console.log('Ambulance is Arriving!!!!');
        console.log(this.status.message.content);
      }
    });
    }

  clickedMarker() {
    console.log('Clicked the marker:');
  }

  mapClicked($event: MouseEvent) {
    console.log('Map Clicked');
  }
}

interface HospitalData {
  lat: number;
  lng: number;
  address: string;
  name: string;
  placeId: string;
  phone: number;
}
